import React, { ReactElement, useCallback, useState, useReducer, useEffect, useMemo } from 'react';
import { Link } from 'react-router-dom';
import ChessCom, { loadChessComMatch } from '../chess/ChessCom';
import * as icons from '../assets/icons/';
import * as regexes from '../utils/regexes';
import { FaLink, FaSearch, FaDownload, FaChessBoard } from 'react-icons/fa';
import { PairingObject, Player, TournamentStatus, TournamentSettings } from '../interfaces';
import { GET } from '../utils/requests';
import styles from '../css/pairings.module.css';
import AutoComplete from '../components/AutoComplete';
import { MatchData } from 'chess-web-api';
import PercentageCompleted from './PieChart';
import { flags } from '../config.json';

export interface PairingsProps {
	id: string
	status: TournamentStatus
	playerDict: Map<string, Player>
	settings: TournamentSettings
}

interface MatchConstructor {
	id: string
	w: Player
	b: Player
	round: number
	link?: string
	linkSource?: 'chessCom' | 'lichess'
	linkType?: 'team' | 'individual'
	teamID?: number

	aborted?: boolean
	bodyStatus?: 'scheduled' | 'finished' | 'in_progress'
	metaStatus?: 'Completed' | 'In Progress' | ''
	registrationTime?: number
	startTime?: number
	endTime?: number
	players?: number
	scored?: boolean
}

export type MatchStatus = 'finished' | 'live' | 'registered' | 'scheduled' | 'pending' | 'aborted';

export class Match {

	public id!: string
	public w!: Player
	public b!: Player

	public teamID?: number
	public link?: string
	public linkSource?: 'chessCom' | 'lichess'
	public linkType?: 'team' | 'individual'

	public aborted?: boolean
	public bodyStatus?: 'scheduled' | 'finished' | 'in_progress'
	public metaStatus?: 'Completed' | 'In Progress' | ''
	public registrationTime?: number
	public startTime?: number
	public endTime?: number
	public players?: number
	public scored?: boolean

	public debugStatus!: string
	constructor(m: MatchConstructor, public round: number) {
		for (let prop of Object.keys(m)) {
			Object.defineProperty(this, prop, {
				value: m[prop as keyof MatchConstructor]
			});
		}
		this.debugStatus = this.status;
	}

	public get status(): MatchStatus {
		if (this.aborted) return 'aborted';
		for (let v of ['Confirmed', 'Finished', 'Live', 'Registered', 'Scheduled']) {
			if (!(('is' + v) in this)) continue;
			if (this[('is' + v) as keyof Match] as boolean) return v.toLowerCase() as  'finished' | 'live' | 'registered' | 'scheduled';
		}
		return 'pending';
	}

	get isConfirmed(): boolean {
		if (this.scored) return true;
		return false;
	}

	get isFinished(): boolean {
		if (this.isConfirmed) return true;
		if (this.bodyStatus === 'finished') return true;
		if (Date.now() > (this.endTime || Infinity)) return true;
		return false;
	}

	get isLive(): boolean {
		if (this.metaStatus === 'In Progress') return true;
		if (this.bodyStatus === 'in_progress') return true;
		if (!this.isFinished) {
			if (this.startTime && Date.now() > this.startTime) return true;
		}
		return false;
	}

	get isRegistered(): boolean {
		if (Date.now() > (this.registrationTime || Infinity)) return true;
		if (this.players) return true;
		if (this.isLive) return true;
		if (this.isFinished) return true;
		return false;
	}

	get isScheduled(): boolean {
		if (Date.now() > (this.startTime || Infinity)) return true;
		if (this.startTime) return true;
		if (this.bodyStatus === 'scheduled') return true;
		if (this.isRegistered) return true;
		if (this.isFinished) return true;
		return false;
	}

}

export default function Pairings(props: PairingsProps & {round: number}): ReactElement {

    const [pairings, setPairings] = useState([] as PairingObject[]);
    const [expanded, setExpanded] = useReducer((state: string[], action: string) => {
		let list = state.slice(0);
		if (state.includes(action)) list.splice(list.indexOf(action));
		else list.push(action);
		return list;
	}, []);
	const [loaded, hasLoaded] = useState(false);

    const [matchDict, setMatch] = useReducer((state: {[key: string]: Match}, action: MatchConstructor) => {
		let res = Object.assign({}, state);
        res[action.id] = new Match(action, action.round);
        return res;
	}, {});
	
	useEffect(() => {
		hasLoaded(false);
		GET({
			url: '/tournament/' + props.id + '/getPairings',
			params: { round: props.round }
		})
			.then(setPairings)
			.then(() => hasLoaded(true))
			.catch(() => {})
	}, [setPairings, props.round, props.id]);
	
	const [choices, setChoices] = useState([] as Player[]);
	const headers = ['White', 'White score', 'Black', 'Black score', 'Game link', 'Time'] as string[];
	const [csv, setCSV] = useState([] as any[][]);

    const populatematchDict = useCallback(async () => {
		if (!pairings.length) return;
		let round = props.round.valueOf();
        for (let i = 0; i < pairings.length; i++) {
			let row = pairings[i];
			let id = [row[0].trim(), row[1].trim()].join('.');
			let w = props.playerDict.get(row[0].trim()) as Player;
			let b = props.playerDict.get(row[1].trim()) as Player;

			let [linkKey, link] = Object.entries(row[2]).find(v => v) || [undefined, undefined];
			let matches = link?.match(regexes.chessCom) as RegExpMatchArray;
			if (matches) link = 'https://www.chess.com/club/matches/live/' + matches[1].trim();
			let teamID = matches ? Number(matches[1]) : undefined;

			let scored = w?.histories[props.round]?.game || b?.histories[props.round]?.game;
			let match = {
				id, teamID, link, w, b, round, scored: !!scored,
				linkSource: linkKey ? linkKey.startsWith('lichess') ? 'lichess' : 'chessCom' : undefined,
				linkType: linkKey ? linkKey.includes('Team') ? 'team' : 'individual' : undefined
			} as MatchConstructor;

			setMatch(match);
			if (teamID) loadChessComMatch(teamID)
				.catch((e: Error | MatchData) => {
					if (e) console.error(e);
					return {} as MatchData;
				})
                .then((body) => {
					if (body) {
						if ((body.start_time && Date.now() < body.start_time * 1000) || body.status === 'finished') {
							setMatch(Object.assign(match, {
								bodyStatus: body.status,
								startTime: body.start_time * 1000,
								endTime: body.end_time ? body.end_time * 1000 : undefined,
								players: body.boards
							}));
							return Promise.reject();
						}
					}
					return body;
				})
                .then((body) => {
					GET({
						url: '/tournament/' + props.id + '/scrapeChessComTeamPage',
						params: {
							url: link
						}
					})
					.then((meta) => {
						if (!meta['Registration Open'] && !meta['Start Time'] && !meta['Finished On']) throw null;
						setMatch(Object.assign(match, {
							registrationTime: meta['Registration Open'] ? new Date(meta['Registration Open']).valueOf() : undefined,
							metaStatus: meta['Status'],
							startTime: body && body.start_time ? body.start_time * 1000 : meta['Start Time'] ? new Date(meta['Start Time']).valueOf() : undefined,
							endTime: meta['Finished On'] ? new Date(meta['Finished On']).valueOf() : undefined,
							players: meta['Players per Team']
						}));
					})
					.catch((e) => {
						if (e) console.error(e);
						setMatch(Object.assign(match, {
							aborted: true
						}));
					});;
				})
				.catch((e) => e ? console.error(e) : null)
				
        }
	}, [pairings, setMatch, props.id, props.playerDict, props.round]);
	
    useEffect(() => {
        populatematchDict();
	}, [populatematchDict]);

	const getDisplayName = useCallback((opp: Player): string => {
        if (!opp || !opp.id) return '\u200b';
		if (opp.id === 'bye') return 'BYE';
		let displayName: string;
		if (props.settings.competitors === 'individual') displayName = [opp.firstName, opp.lastName].join(' ');
		else displayName = opp.firstName;
		return displayName;
	}, [props.settings.competitors]);

	const rows = useMemo(() => {
		let _csv = [] as any[][];
		let r = Object.values(matchDict).map((match, i) => {
			if (match.round !== props.round) return null;
			let displayTime = match.startTime || match.registrationTime;
			let date = new Date(displayTime || 0).toString();
			let data = [
				getDisplayName(match.w),
				match.w?.histories[props.round]?.game ?? '',
				getDisplayName(match.b as Player),
				match.b?.histories[props.round]?.game ?? '',
				displayTime ? date.slice(0, 10) + '\n' + date.slice(16, 21): '',
				match.link,
				match.w ? match.w.nationality : '',
				match.b ? match.b.nationality : ''
			] as string[];

			_csv[i + 1] = data;

			return <Row key={['row', i].join('.')} data={data} match={match} round={props.round} expanded={expanded.includes(match.id)} setExpanded={setExpanded}
				hidden={!!choices.length && !choices.some(c => c.id === match.w.id) && !choices.some(c => c.id === match.b.id)}
				settings={props.settings}
			/>
		});
		setCSV(_csv);
		return r;
	}, [getDisplayName, setExpanded, setCSV, expanded, choices, matchDict, props.round, props.settings]);

	const handleDownload = useCallback(() => {
		const element = document.createElement('a')
		const fileContent = headers.join(',') + '\n' + csv.map(line => line.join(',')).join('\n');
		const fileFormat = 'csv'
		const file = new Blob([fileContent], {type: 'text/csv'});
		const tourName = (props.status.name.match(regexes.lowerCase) || []).join('');
		const date = new Date(Date.now()).toString().slice(4, 15);
	
		element.href = URL.createObjectURL(file)
		element.download = `${tourName}_${date}_${[props.round]}.${fileFormat}`
		element.click()
	}, [csv, props.round, headers, props.status.name]);

	const CompletedChart = useMemo(() => {
		return <PercentageCompleted matches={matchDict} round={props.round} total={pairings.length}/>;
	}, [matchDict, props.round, pairings]);

	return <>
		<section>
			{CompletedChart}
		</section>
		<section>
			<div className={styles.subheader}>
				<div>All times in {new Date(Date.now()).toString().slice(25)}</div>
				<div className={['button'].join(' ')} onClick={handleDownload}>
					<div><FaDownload /></div>
					<div>Download</div>
				</div>
				<div>
					<div className={styles.searchBar}>
						<FaSearch />
						<AutoComplete
							onChange={(options: Player[]) => setChoices(options)}
							onSubmit={() => Promise.resolve()}
							action=''
							options={Array.from(props.playerDict.values())}
							convert={(p: Player) => getDisplayName(p)}
							styles={styles}
							placeholder={'Search'}
						/>
					</div>
				</div>
			</div>			
			
			<table className='download-table full-width pairings'>
				<thead>
					<tr>
						<td style={{width: '5%'}}>Fed</td>
						<td style={{width: '22.5%'}}>White</td>
						<td style={{width: '12.5%'}}>Link</td>
						<td style={{width: '5%'}}></td>
						<td style={{width: '10%'}}>Start Time</td>
						<td style={{width: '5%'}}></td>
						<td style={{width: '12.5%'}}>Watch Live</td>
						<td style={{width: '22.5%'}}>Black</td>
						<td style={{width: '5%'}}>Fed</td>
					</tr>
				</thead>
				<tbody>
					{loaded && !pairings.length ? <tr><td colSpan={100}>Draw has not yet taken place!</td></tr> : null}                       
					{rows}
				</tbody>
			</table>
		</section>
	</>;

}

interface RowProps {
	data: any[]
	match: Match
	hidden: boolean
	round: number
	expanded: boolean
	setExpanded: (id: string) => void
	settings: TournamentSettings
}

function Row(props: RowProps) {

	const match = props.match;
	const data = props.data;

	return (
		<>
			<tr key={['row'].join('.')} style={{
				display: props.hidden ? 'none' : undefined
			}}>
				<td>
					<div className={styles.flag}>{data[6] ? <img src={flags.replace('|',data[6])} alt={data[6]}/> : null}</div>
				</td>
				<td id={match.w?.id}>
					<Link to={{
						pathname: ['', props.settings.competitors === 'individual' ? 'player' : 'team', match.w?.id].join('/'),
						state: { from: 'pairings' }
					}} className='playerName'>{data[0]}</Link>
				</td>
				<td className={styles.inner}>
					{match.link ? <a className='button' href={match.link} target='_blank' rel='noopener noreferrer'>
						<FaLink />
						<div>chess.com</div>
					</a> : null}
				</td>     
				<td className={styles.inner}>
					<div className={['bold', styles.score, styles[match.status], match.status].join(' ')}>
						{data[1]}
					</div>
				</td>
				<td style={{whiteSpace: 'pre-line'}}>{data[4] || '-'}</td>
				<td className={styles.inner}>
					<div className={['bold', styles.score, styles[match.status], match.status].join(' ')}>
						{data[3]}
					</div>
				</td>
				<td className={styles.inner}>
					{match.link && match.isScheduled ? <div className='button' onClick={() => match.isFinished ? props.setExpanded(match.id) : null}>
						{!match.isFinished ? <>
							{match.isLive ? <img className='icon' src={icons.watch} alt={'Watch games'}/> : <FaChessBoard />}
							<a href={'https://www.chess.com/live#tm=' + match.teamID} target='_blank' rel='noopener noreferrer'>
								{match.isLive ? ' Watch live' : 'Join Game'}
							</a>
						</> : <div>Analysis</div>}
					</div> : null}
				</td>
				<td id={match.b?.id}>
					<Link to={{
						pathname: ['', props.settings.competitors === 'individual' ? 'player' : 'team', match.b?.id].join('/'),
						state: { from: 'pairings' }
					}} className='playerName'>{data[2]}</Link>
				</td>
				<td>
					<div className={styles.flag}>{data[7] ? <img src={flags.replace('|',data[7])} alt={data[7]}/> : null}</div>
				</td>
			</tr>
			{match.link && props.expanded ? <tr key={['row', 'dropdown'].join('.')}>
				<td colSpan={100}>
					{match.linkSource === 'chessCom' ?
						match.linkType === 'team' ?
							<ChessCom
								matchURL={match.link}
								enforceBoards={match.players ? Number(match.players) : 0}
								round={props.round}
							/> :
							null :
						null
					}
				</td>
			</tr> : null}
		</>
	)
}