import React, { useEffect, useState, useCallback, useMemo } from 'react';
import { StaticContext } from 'react-router';
import Markdown from 'react-markdown';
import { Route, Redirect, Link, Switch, RouteComponentProps } from 'react-router-dom';

import * as regexes from '../utils/regexes';
import styles from '../css/home.module.css';
import { GET, socket } from '../utils/requests';
import { Notif, TournamentStatus, ProfileData } from '../interfaces';
import { FaAngleRight, FaAngleLeft } from 'react-icons/fa';
import { TournamentSettings } from '../resources/settings';

export default function Profile(props: RouteComponentProps<any, StaticContext, any> & {
	profile: ProfileData
	updateProfile: () => void
}) {

	const isAuthed = useMemo(() => true, []);
	const [tournaments, setTournaments] = useState([] as (TournamentStatus & { setting: TournamentSettings })[]);
	const animate = useMemo(() => props.history.location.state && props.history.location.state.from, [props.history.action]);

	const updateTournaments = useCallback(() => GET({ url: '/tournaments' })
		.then(setTournaments), [setTournaments]);

	useEffect(() => {
		updateTournaments();
		props.updateProfile();
	}, [updateTournaments, props.updateProfile]);
	return (<>
        <div id='main' className={animate ? 'animated-left' : ''}>
			<div className={styles.container}>
				<div className={styles.title}>
					<div className='container'>
						<h1><span className='bold'>{props.profile.username}</span></h1>
						<div>Email: {props.profile.email}</div>
						<div>Account creation: {new Date(props.profile.createdAt).toString().slice(0, 15)}</div>
						<div>Tournaments: {tournaments.length}</div>
						<div>Public tournaments: {tournaments.filter(t => t.setting && t.setting.public).length}</div>
					</div>
				</div>
				<div className={['container', styles.yourTournaments].join(' ')}>
					<table className={[styles.tableAnnounce].join(' ')}>
						<thead>
							<tr>
								<th colSpan={5} className={styles.sectionHeader}>
									Your Tournaments
								</th>
							</tr>
							<tr>
								<th style={{width: '20px' }}>Manage</th>
								<th>Name</th>
								<th>Started</th>
								<th>Round</th>
								<th>View</th>
							</tr>
						</thead>
						<tbody>
							{tournaments.sort((a, b) => new Date(b.updatedAt).valueOf() - new Date(a.updatedAt).valueOf()).map((tourn, i) => {
								let dateStarted = new Date(tourn.createdAt).toString();
								return (
									<tr key={[tourn.id, i].join('.')} className={styles.rowLink}>
										<td><a className={styles.tournamentLink} href='https://beta.scorchapp.co.uk'>
											<FaAngleLeft />
										</a></td>
										<td>
											{tourn.name || tourn.id}
										</td>
										<td>
											{dateStarted.slice(0, 11)}
										</td>
										<td>
											{tourn.round}
										</td>
										<td>
											{tourn.setting && tourn.setting.public ?
											<Link className={styles.tournamentLink} to={{pathname: '/' + tourn.id, state: {from: props.location.pathname}}}>
												<FaAngleRight />
											</Link>
											: null}
										</td>
									</tr>
								);
							})}				
						</tbody>
					</table>
				</div>
				<div>
					<a href='https://beta.scorchapp.co.uk' rel='noopener noreferrer' target='_blank' className={['header-button', styles.new].join(' ')}>
						Create New Tournament
						<FaAngleRight />
					</a>
				</div>
			</div>				
		</div>
	</>)
}