import React, { ReactElement, useState, useCallback, useMemo } from 'react';
import { Redirect } from 'react-router-dom';
import url from 'url';
import { server, FORM, cookies } from '../utils/requests';

import styles from '../css/login.module.css';
import { IoIosArrowForward, IoIosArrowBack } from 'react-icons/io';

export function Crown(props: {
	loading: boolean,
	setLoading: (loading: boolean) => void
	classNames?: {[key: string]: string}
}) {

	return (
		<div
			className={[styles.logo, props.classNames?.logo].join(' ')}
		>
			<img style={{visibility: props.loading ? 'hidden' : 'visible'}} className={styles.staticLogo} src={process.env.PUBLIC_URL + '/favicon.png'} alt='swiss-logo.png'/>

			<img style={{visibility: props.loading ? 'visible' : 'hidden'}} className={styles.staticLogo} src={process.env.PUBLIC_URL + '/images/loading_queen.png'} alt='swiss-logo.png'/>
			<img style={{visibility: props.loading ? 'visible' : 'hidden'}} className={styles.crownOutline} src={process.env.PUBLIC_URL + '/images/crown-outline.png'} alt='outline-gif'/>
			<img style={{visibility: props.loading ? 'visible' : 'hidden'}} className={styles.loadingGif} src={process.env.PUBLIC_URL + '/images/loading-crown.gif'} alt='loading-gif'/>
		</div>
	);
}

interface LoginProps {
	mode: 'login' | 'register'
	login: (set: boolean) => void
	updateProfile: () => void
}
export default function Login(props: LoginProps) {

	const [session, setSessionID] = useState('');
	const [loading, setLoading] = useState(false);
	const [register, setRegister] = useState(props.mode === 'register');
	const [errorMessage, triggerError] = useState('');

	const login = useCallback(async (sessionID: string): Promise<void> => {
		await cookies.set('sessionID', sessionID );
		props.login(true);
		setSessionID(sessionID);
	}, [setSessionID, props.login, props.updateProfile]);

	const handleSubmit = useCallback((e: React.FormEvent<HTMLFormElement>): void => {
		setLoading(false);
		FORM(e)
			.then((sessionID: string) => login(sessionID))
			.catch((e) => {
				if (e && e.response && e.response.data) triggerError(e.response.data);
				console.error(e);
			})
			.finally(() => {
				setLoading(false);
			});
	}, [setLoading, triggerError, login]);

	const loginForm = useMemo((): ReactElement => {
		return (
			<form
				className={styles.form}
				method='POST'
				onSubmit={handleSubmit}
				action={url.resolve(server, 'login')}
			>
				<input type='text' name='username' required placeholder='username' className={['input', styles.input].join(' ')}></input>
				<input type='password' name='password' required placeholder='password' className={['input', styles.input].join(' ')}></input>
				<div className={styles.forgottenContainer}>
					<div className={['input', styles.forgotten].join(' ')}>username reminder</div>
					<div className={['input', styles.forgotten].join(' ')}>password reset</div>
				</div>
				<input type='submit' value='LOGIN' className={['input', styles.submit].join(' ')}></input>
			</form>
		);
	}, [handleSubmit]);

	const registerForm = useMemo((): ReactElement => {
		return (
			<form
				className={styles.form}
				method='POST'
				onSubmit={handleSubmit}
				action={url.resolve(server, 'register')}
			>
				<input type='text' name='username' required placeholder='username' className={['input', styles.input].join(' ')}></input>
				<input type='password' name='password' required placeholder='password' minLength={8} className={['input', styles.input].join(' ')}></input>
				<input type='email' name='email' required placeholder='email' className={['input', styles.input].join(' ')}></input>
				<input type='submit' value='REGISTER' className={['input', styles.submit].join(' ')}></input>
			</form>
		);
	}, [handleSubmit]);

	const crownBuild = useMemo(() => <Crown setLoading={(l: boolean) => setLoading(l)} loading={false} />, [setLoading]);

	const loginSection = useMemo((): ReactElement => {
		return (
			<div className={[styles.loginSection, styles.initialiseButton].join(' ')}>
				<div className={styles.loginBackground}>
					<div className={[styles.flip].join(' ')} onClick={() => {
						setLoading(true);
						setRegister(false);
						triggerError('');
						setTimeout(() => setLoading(false), 500);
					}}>
						<div>LOGIN</div>
						<div>
                            <IoIosArrowBack />
                        </div>
					</div>
					<div></div>
					<div className={[styles.flip].join(' ')} onClick={() => {
						setLoading(true);
						setRegister(true);
						triggerError('');
						setTimeout(() => setLoading(false), 500);
					}}>
						<div>REGISTER</div>
						<div>
                            <IoIosArrowForward />
                        </div>
					</div>
				</div>
				<div className={['container', styles.foreground, register ? styles.registerForeground : styles.loginForeground].join(' ')}>
					{crownBuild}
					{
						register ? registerForm : loginForm
					}
				</div>
			</div>
		);
	}, [setLoading, setRegister, triggerError, register, loginForm, registerForm, crownBuild]);

	if (session) return <Redirect to={{
		pathname: '/profile',
		state: { from: '/login' }
	}}/>

	return (
		<div id='main'>
			<div className={[styles.column, 'container'].join(' ')}>
				<div className={styles.frame}>
					{loginSection}
				</div>
			</div>
			<div className={styles.messageBar}>
				{errorMessage || '\u200b'}
			</div>
		</div>
	);


}