import React, { useEffect, useState, useMemo, ReactElement, useContext, useCallback, useReducer } from 'react';
import { render } from 'react-dom';
import { StaticContext } from 'react-router';
import { Route, Redirect, Link, Switch, RouteComponentProps } from 'react-router-dom';
import './App.css';
import { TournamentStatus, Player, TournamentSettings, TournamentPlayers } from './interfaces';
import { GET } from './utils/requests';
import { ModeSwitcher, RoundSwitcher } from './tournament/RoundSwitcher';
import CrossTable from './tournament/Standings';
import Chart from './tournament/Stats';
import NotFound from './404';
import styles from './css/nav.module.css';
import { FaLink, FaWindowClose } from 'react-icons/fa';
import { IoMdDocument } from 'react-icons/io';
import Pairings from './tournament/Pairings';
import * as regexes from './utils/regexes';
import TV from './tournament/Streams';
import Draggable from './components/DraggableSticky';
import { Img } from './components/Image';
import { capitalise } from './utils/prototype';
import { TieBreaks } from './resources/tiebreaks';
import { TiebreakContext, DisplayNameContext } from './Contexts';
import Results from './tournament/Results';
import usePlayers from './tournament/Standings/Players';
import PlayerData from './models/Player';

export default function Tournament(props: RouteComponentProps<any, StaticContext, any>) {

    const id = props.match.params.id
	const [loaded, setLoaded] = useReducer((state: (boolean | undefined)[], action: boolean) => {
		let index = state.findIndex(v => v === undefined);
		let arr = state.slice(0);
		arr[index] = action;
		return arr;
	}, [undefined, undefined, undefined, undefined]);
    const [settings, setSettings] = useState({} as TournamentSettings);
    const [status, setStatus] = useState({} as TournamentStatus);
    const [state, setState] = useState([] as string[]);
	const [rawPlayers, setPlayers] = useState([] as Player[]);
	const animate = useMemo(() => props.history.location.state && props.history.location.state.from === '/', [props.history.action]);
	const [poppedComponent, pop] = useState(null as null | HTMLDivElement);

	const players: PlayerData[] = usePlayers({ status, players: rawPlayers, settings, round: status.round });

    const playerDict: Map<string, PlayerData> = useMemo(() => {
        if (!players.length) return new Map();
        let dict = new Map();
		for (let p of players) dict.set(p.id, p);
        return dict;
    }, [players]);

	const round = status.round || 0;

	useEffect(() => {
		GET({ url: '/tournament/' + id + '/fetchPlayers'})
			.then(setPlayers)
			.then(() => setLoaded(true))
			.catch(() => setLoaded(false));
	}, [setPlayers, id, setLoaded]);
	
	useEffect(() => {
		GET({ url: '/tournament/' + id + '/getSettings'})
			.then(setSettings)
			.then(() => setLoaded(true))
			.catch(() => setLoaded(false));
	}, [setSettings, id, setLoaded]);
	
	useEffect(() => {
		GET({ url: '/tournament/' + id + '/getState'})
			.then(({ players }: { players: TournamentPlayers }) => {
				let order = Object.entries(players)
					.map(([k, v]) => [Number(k), v] as [number, string[]])
					.sort((a, b) => b[0] - a[0])
					.reduce((acc, curr) => acc.concat(curr[1]), [] as string[]);
				setState(order);
			})
			.then(() => setLoaded(true))
			.catch(() => setLoaded(false));
	}, [setState, id, setLoaded]);

    useEffect(() => {
		GET({ url: '/tournament/' + id + '/getStatus'})
			.then(setStatus)
			.then(() => setLoaded(true))
			.catch(() => setLoaded(false));
    }, [setStatus, setLoaded, id]);

    useEffect(() => {
        if (!round) return;
        let histories = new Array(round + 1);
        for (let i = 1; i < round + 1; i++) histories[i] = {
            match: 0,
            game: 0
        };
        playerDict.set('bye', PlayerData.bye(id, round, settings));
	}, [playerDict, round, id, settings]);

	const [showTB, setShowTB] = useState(false);
	const tieBreaks = useMemo(() => {
		let entries = Object.entries(TieBreaks);
		if (settings.tieBreaks) {
			let arr = settings.tieBreaks.split(',');
			entries = entries.sort((a, b) => arr.indexOf(a[0]) - arr.indexOf(b[0]));
		}
		return entries;
	}, [settings.tieBreaks]);

	const getDisplayname = useCallback((id: string | Player): string => {
		if (!id) return '\u200b';
		if (id === 'bye') return 'BYE';
		if (typeof id !== 'string') {
			let p = id as Player;
			if (settings.competitors === 'individual') return [p.firstName, p.lastName].join(' ');
			else return p.firstName;
		}
		let opp = playerDict.get(id);
		if (!opp) return id;
		let displayName: string;
		if (settings.competitors === 'individual') displayName = [opp.firstName, opp.lastName].join(' ');
		else displayName = opp.firstName;
		return displayName;
	}, [playerDict, settings.competitors]);

	if (loaded.some(v => v === undefined)) return null;
	if (loaded.some(v => v === false)) return <NotFound />;

    return (

        <div id='main' className={animate ? 'animated-right' : ''}>
			<div className='container' style={{ marginTop: '60px' }}>
				<div className={styles.stage}>
					<div className={styles.profilePicture}>
						{settings.logoURL ?
							<Img src={settings.logoURL} alt='profile_picture' /> : 
							<div className={styles.defaultProfilePicture}>
								{status.name.match(regexes.lowerCase)?.map(v => v.substring(0, 1).toUpperCase()).join('')}
							</div>
						}</div>
					<div className={styles.nameContainer}>
						<div className={styles.name}>
							{status.name}
						</div>
					</div>
					{settings.profileURL ? <a className={[styles.website].join(' ')} 
						href={settings.profileURL}
						target='_blank'
						rel='noopener noreferrer'
					>
						<FaLink /> Website
					</a> : null}
					{settings.rulesURL ? <a className={[styles.rules].join(' ')} 
						href={settings.rulesURL}
						target='_blank'
						rel='noopener noreferrer'
					>
						<IoMdDocument /> Rules
					</a> : null}
					<div className={styles.infoBox}>
						<div>
							<div>Director: </div><div>{status.user}</div>
							<div>Created: </div><div>{new Date(status.createdAt).toString().slice(4, 15)}</div>
							<div>{capitalise(settings.competitors === 'team' ? 'Teams' : 'Players')}: </div><div>{status.players}</div>
							<div>Format: </div><div>{capitalise(settings.pairingSystem)}</div>
						</div>
						<div>
							<div>Location: </div><div>{settings.location || '-'}</div>
							<div>Time control: </div><div>{settings.timeControl || '-'}</div>
							<div>Status: </div><div>{status.active !== false ? 'Live' : status.round ? 'Finished' : 'Not yet started'}</div>
							<div className={styles.tieBreakLink} onClick={() => setShowTB(!showTB)}>Tie Breaks</div>
						</div>
					</div>


					<Route path='/:id/:section/:round?/' render={(rProps) => {
						let round = parseInt(rProps.match.params.round);
						if (isNaN(round)) round = status.round;
						if (round < 0 || round > settings.totalRounds) return null;
						return <ModeSwitcher
							id={props.match.params.id}
							settings={settings}
							section={rProps.match.params.section}
						/>
					}} />
				</div>
			</div>
			<section>
				<div className={['container', styles.tieBreakContainer, showTB ? styles.visible : ''].join(' ')}>
					<div>
						<div className={styles.tieBreakTitle} onClick={() => setShowTB(false)}>
							TIE BREAKS
						</div>
						<div className={styles.tieBreaks}>
							{tieBreaks.map(([, v], i) => <>
								<div>#{i + 1}</div>
								<div>{v.name}</div>
							</>)}
						</div>
					</div>
				</div>
			</section>
			<Route path='/:id/:section/:round?/' render={(rProps) => {
				let round: number;
				if (rProps.match.params.round === 'results') round = status.round + 1;
				else round = parseInt(rProps.match.params.round);
				if (isNaN(round)) round = status.round;
				if (settings.totalRounds === 'Infinity') settings.totalRounds = Infinity as number;
				if (round < 0 || round > settings.totalRounds + 1) return null;
				let section = rProps.match.params.section;
				if (section !== 'pairings' && section !== 'standings') return null;
				return <RoundSwitcher
					section={rProps.match.params.section}
					round={round}
					id={props.match.params.id}
					playerDict={playerDict}
					status={status}
					settings={settings}
				/>
			}} />
			<Route render={({location}) => {
				return (
					<Switch location={location}>
						<Route exact path='/:id/' render={() => {
							return <Redirect push to={{
								pathname: `/${settings.vanityURL || props.match.params.id}/pairings`,
								state: props.history.location.state
							}}/>
						}}/>
						<Route path='/:id/stats/' render={() => {
							return <Chart
								round={status.round}
								status={status}
								players={Array.from(playerDict.values())}
								settings={settings}
							/>
						}} />
						<Route path='/:id/tv/' render={() => {
							return <TV
								streamURL={settings.streamURL}
								pop={pop}
							/>
						}} />
						<Route exact path='/:id/:section/' render={(rProps) => {
							return <Redirect push to={{
								pathname: rProps.match.url + (rProps.match.url.endsWith('/') ? '' : '/') + (status.round && !status.active ? 'results' : status.round),
								state: props.history.location.state
							}}/>
						}} />
						<Route path='/:id/pairings/:round' render={(rProps) => {
							if (rProps.match.params.round === 'results') {
								return <DisplayNameContext.Provider value={getDisplayname}>
									<Results state={state}  playerDict={playerDict} settings={settings} id={rProps.match.params.id} />
								</DisplayNameContext.Provider>
							}
							let round = parseInt(rProps.match.params.round);
							if (isNaN(round)) return null;
							if (round < 0 || round > settings.totalRounds) return null;
							return <Pairings
								round={round}
								id={props.match.params.id}
								playerDict={playerDict}
								status={status}
								settings={settings}
							/>
						}}/>
						<TiebreakContext.Provider value={tieBreaks}>
							<Route path='/:id/standings/:round' render={(rProps) => {
								if (rProps.match.params.round === 'final') return <Redirect push to={{
									pathname: (rProps.match.url + (rProps.match.url.endsWith('/') ? '' : '/')).split('/').slice(0, -2).join('/') + '/' + settings.totalRounds,
									state: rProps.history.location.state
								}} />
								let round = parseInt(rProps.match.params.round);
								if (isNaN(round)) return null;
								if (round < 0 || round > settings.totalRounds) return null;
								return <CrossTable
									round={round}
									setDisplayPlayer={() => {}}
									show={() => {}}
									status={status}
									players={players}
									settings={settings}
									state={state}
								/>
							}} />
						</TiebreakContext.Provider>
						<Route path='/:id/*' component={NotFound} status={404} />
					</Switch>
				);
			}}/>
			<Draggable margin={10}>
				{poppedComponent ? <div onClick={() => pop(null)}><FaWindowClose /></div> : null}
				<div dangerouslySetInnerHTML={{
					__html: poppedComponent?.outerHTML || ''
				}}/>
			</Draggable>
        </div>
    );
}